.media {
    margin: 0 10px;
}

.media__text {
    overflow:hidden;
    _overflow:visible;
    zoom:1;
}

.media__img {
    padding-bottom: 60px;

    @media only screen and (min-width: $media-tablet) {
        display: none;
    }
    @media only screen and (min-width: $media-desktop) {
        display: block;
        padding-bottom: 0;
        float:left;
        margin-right: 30px;
    }
}

.media__img img{
    display:block;
}
